@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Montserrat", sans-serif;padding:45px 1rem;background-color:#1a1a1a}.instructions{color:#acacac;text-align:center}.search-container{max-width:1100px;width:90vw;min-height:90vh;margin:0 auto;padding:45px;border-radius:3px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.search-container h2{color:#ffffff}.search-container p{height:30px;font-size:20px;margin-top:2rem}.search-bar{height:45px;width:480px;font-size:18px;padding:1rem;outline:none;border:none;margin-top:2rem;border-radius:15px}.input-wrapper{position:relative}.input-wrapper .fa-times{position:absolute;height:35px;width:35px;right:5px;bottom:5px;text-align:center;line-height:35px;font-size:18px;cursor:pointer;color:#adadad;border-radius:50%;transition:all 0.15s ease-out}.input-wrapper .fa-times:hover{background-color:#e2e2e2}.search-items{-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none;-ms-overflow-style:none;scrollbar-width:none;background-color:#ffffff;border:1px solid #d8d8d8;border-top:none;border-radius:15px;padding:1rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:480px;max-height:40vh;overflow:hidden;overflow-y:auto;position:absolute;top:110%;font-size:14px;box-shadow:rgba(0,0,0,0.12) 0px 1px 3px,rgba(0,0,0,0.24) 0px 1px 2px}.search-items::-webkit-scrollbar{width:0 !important}.search-items .search-item{display:block;padding:8px 1rem 8px 0.5rem;color:#282828;transition:all 0.15s ease-out;text-decoration:none;position:relative;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;border-radius:10px}.search-items .search-item .search-item-link{color:#569dd8ad;border-radius:3px;margin-right:0.5rem;padding:8px;border-radius:50%;transition:all 0.15s ease-out}.search-items .search-item .search-item-link:hover{background-color:#d1d1d1}.search-items .search-item .bookImg{height:20px;width:20px}.search-items .search-item:hover{background-color:#f5f5f5}.item-info .item-author{font-size:12px;color:#cecece}.item-details{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-left:auto;text-align:right}.pagesCount,.item-country{font-size:10px;font-weight:600;color:#cecece;left:85%}.item-country span{color:#5c5c5c}

